// react
// component
// prop type
// from react
import React, { Component, PropTypes } from 'react';
// connect state and action, from react redux
import { connect } from 'react-redux';
// one of the action
import { getAllJobs } from '../actions/jobs';

const config = require('../../config');

// component class item list, extends component
class JobsList extends Component {
    // when mount
    componentDidMount() {
      // where this fetch data
      this.props.fetchData(config.backendUserDataUrl);
    }

    //
    render() {
      // error
      if (this.props.haveError) {
          return <p>Sorry! Fetching jobs have error.</p>;
      }

      // loading
      if (this.props.areLoading) {
          return <p>Loading…</p>;
      }

      //
      // item id and item label
      return (
        <p>{ this.props.user }</p>
      );
    }
}

// map state to attribute
const mapStateToProps = (state) => {
  // state
  return {
    // state has items
    jobs: state.jobs,
    // state has error
    haveError: state.haveError,
    // state has loading
    areLoading: state.areLoading
  };
};

// map dispatch to attribute
const mapDispatchToProps = (dispatch) => {
  // send === dispatch
  return {
    // fetch as attribute
    // it is a func with url, then dispatch item fetch data (url)
    fetchData: (url) => dispatch(getAllJobs(url))
  };
};

// now export default
// connect attribute and func
// with this component item list
export default connect(mapStateToProps, mapDispatchToProps)(JobsList);



